<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="container">
      <!--  -->
      <el-input placeholder="" v-model="input" class="input-with-select" @change="inputChange">
        <el-button slot="append" icon="el-icon-search" @click="inputChange"></el-button>
      </el-input>
      <el-button type="primary" style="margin-left: 20px" @click="addGoods"></el-button>
      <!--  -->
      <el-table :data="goodsList" border style="width: 100%;margin:20px 0px">
        <el-table-column prop="idx" label="#" width="60">
        </el-table-column>
        <el-table-column prop="goods_name" label="">
        </el-table-column>
        <el-table-column prop="goods_price" label="()" width="120">
        </el-table-column>
        <el-table-column prop="goods_weight" label="" width="90">
        </el-table-column>
        <el-table-column label="" width="160">
          <template v-slot="scoped">
            <span>{{scoped.row.add_time | dateFormat('yyyy-MM-dd')}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="150">
          <template v-slot="scoped">
            <el-button type="primary" icon="el-icon-edit" class="operationBtn"
              @click="dialogFormVisible = true;editCurrent(scoped.row.goods_id)">
            </el-button>
            <el-button type="danger" icon="el-icon-delete" class="operationBtn"
              @click="deleteGoods(scoped.row.goods_id)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisible" width="45%" @close="resetField">
        <el-form :model="ruleFormNess" :rules="rules" ref="ruleFormNess" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="name">
            <el-input v-model="ruleFormNess.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="price">
            <el-input v-model="ruleFormNess.price"></el-input>
          </el-form-item>
          <el-form-item label="" prop="number">
            <el-input v-model="ruleFormNess.number"></el-input>
          </el-form-item>
          <el-form-item label="" prop="weight">
            <el-input v-model="ruleFormNess.weight"></el-input>
          </el-form-item>
        </el-form>
        <el-form ref="ruleForm" :model="ruleForm" label-width="100px">
          <el-form-item label="" prop="introduce">
            <el-input v-model="ruleForm.introduce"></el-input>
          </el-form-item>
          <el-form-item label="" prop="url">
            <el-input v-model="ruleForm.url"></el-input>
          </el-form-item>
          <el-form-item label="" prop="params">
            <el-input v-model="ruleForm.params"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="dialogFormVisible = false;editGoodsList()"> 
          </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pagenum"
        :page-sizes="[3, 5, 10, 15]" :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      input: '',
      // 
      pagenum: 1,
      // 
      pagesize: 5,
      // 
      total: 0,
      // 
      dialogFormVisible: false,
      // 
      // 
      ruleFormNess: {
        id: '',
        name: '',
        price: '',
        number: '',
        weight: ''
      },
      // 
      ruleForm: {
        introduce: '',
        url: '',
        params: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        weight: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      goodsList: []
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // 
    handleSizeChange(val) {
      this.pagesize = val
      this.getGoodsList()
    },
    handleCurrentChange(val) {
      this.pagenum = val
      this.getGoodsList()
    },
    // 
    getGoodsList() {
      this.$http.get('goods', {
        params: {
          query: this.input,
          pagenum: this.pagenum,
          pagesize: this.pagesize
        }
      }).then(response => {
        if (response.data.meta.status === 200) {
          this.$message({
            message: '!',
            type: 'success'
          })
          this.goodsList = response.data.data.goods
          this.goodsList.forEach((item, index) => {
            item.idx = index + 1
          })
          this.total = response.data.data.total
        } else {
          this.$message({
            message: '!',
            type: 'error'
          })
        }
      })
    },
    // 
    inputChange() {
      this.getGoodsList()
    },
    // 
    deleteGoods(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$http.delete(`goods/${id}`).then(response => {
          if (response.data.meta.status === 200) {
            this.$message({
              type: 'success',
              message: '!'
            })
            this.getGoodsList()
          } else {
            this.$message({
              type: 'error',
              message: '!'
            })
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    resetField() {
      this.$refs.ruleForm.resetFields()
      this.$refs.ruleFormNess.resetFields()
    },
    // id
    editCurrent(id) {
      this.ruleForm.id = id
    },
    // 
    editGoodsList() {
      this.$http.put(`goods/${this.ruleFormNess.id}`, {
        goods_name: this.ruleFormNess.name,
        goods_price: this.ruleFormNess.price,
        goods_number: this.ruleFormNess.number,
        goods_weight: this.ruleFormNess.weight,
        goods_introduce: this.ruleForm.introduce,
        pics: this.ruleForm.url,
        attrs: this.ruleForm.params
      }).then(response => {
        if (response.data.meta.status === 201) {
          this.$message({
            message: '',
            type: 'success'
          })
          this.getGoodsList()
        } else {
          this.$message({
            message: ''
          })
          console.log(response.data)
        }
      })
    },
    // 
    addGoods() {
      this.$router.push('/addgoods')
    }
  }
}
</script>

<style lang="less" scoped>
@deep: ~">>>";
.container {
  width: 96%;
  background-color: #fff;
  margin: 20px auto 0 auto;
  padding: 20px;
  box-shadow: 0 0 6px #ccc;
  // 
  .input-with-select {
    width: 400px;
  }
  @{deep} .el-select-dropdown {
    top: 577px !important;
    @{deep} .el-scrollbar__wrap {
      overflow-x: none !important;
    }
  }
  .operationBtn {
    height: 30px !important;
    line-height: 30px;
    padding: 0 20px;
  }
}
</style>
