<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="container">
      <el-button type="primary" class="addClass" @click="dialogVisible = true"></el-button>
      <el-table :data="categoriesLists" style="width: 100%;margin-bottom: 20px;" row-key="cat_id" border
        :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
        <el-table-column prop="idx" label="#" width="60">
        </el-table-column>
        <el-table-column prop="cat_name" label="">
        </el-table-column>
        <el-table-column prop="cat_deleted" label="">
          <i class="el-icon-success"></i>
        </el-table-column>
        <el-table-column prop="cat_level" label="">
          <template v-slot="scoped">
            <el-tag v-if="scoped.row.cat_level == 0"></el-tag>
            <el-tag type="success" v-else-if="scoped.row.cat_level == 1"></el-tag>
            <el-tag type="warning" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template v-slot="scoped">
            <el-button type="primary" icon="el-icon-edit"
              @click="editDialogVisible = true;getCurrentId(scoped.row.cat_id)">
            </el-button>
            <el-button type="danger" icon="el-icon-delete" @click="deleteClass(scoped.row.cat_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="" :visible.sync="editDialogVisible" width="45%" @close="resetRulesField">
        <el-form :model="editRuleForm" :rules="rules" ref="editRuleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="name">
            <el-input v-model="editRuleForm.name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editDialogVisible = false;editClassification()"> </el-button>
        </span>
      </el-dialog>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[3, 5, 10, 15]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
      <el-dialog title="" :visible.sync="dialogVisible" width="45%" append-to-body @close="resetField">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="name">
            <el-input v-model="ruleForm.name" style="width:89.8%"></el-input>
          </el-form-item>
          <div class="parentClassification" style="padding-left: 32px">
            <span class="el-form-item__label"></span>
            <el-cascader ref="cascader" :options="options" :props="{ checkStrictly: true }" style="width:80%" clearable
              @change="getParentPid">
            </el-cascader>
          </div>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="dialogVisible = false;addClassification()"> </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      categoriesLists: [],
      // 
      pageNum: 1,
      // 
      pageSize: 5,
      // 
      total: 0,
      // 
      dialogVisible: false,
      // 
      editDialogVisible: false,
      // 
      ruleForm: {
        name: '',
        // 
        parentClassification: ''
      },
      // 
      editRuleForm: {
        name: ''
      },
      // 
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      options: [],
      // 
      pid: 0,
      level: 0,
      // id
      currentId: 0
    }
  },
  created() {
    this.getCategoriesLists()
    this.getCategories()
  },
  methods: {
    // 
    getCategoriesLists() {
      this.$http.get('categories', { params: { pagenum: this.pageNum, pagesize: this.pageSize } }).then(response => {
        this.categoriesLists = response.data.data.result
        this.total = response.data.data.total
        this.categoriesLists.forEach((item, index) => {
          item.idx = index + 1
        })
      })
    },
    // 
    getCategories() {
      this.$http.get('categories', { params: { type: 2 } }).then(response => {
        this.options = response.data.data
        this.options.forEach(item => {
          item.value = item.cat_id
          item.label = item.cat_name
          if (item.children !== undefined) {
            item.children.forEach(itemchild => {
              itemchild.value = itemchild.cat_id
              itemchild.label = itemchild.cat_name
            })
          }
        })
      })
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getCategoriesLists()
    },
    handleCurrentChange(val) {
      this.pageNum = val
      this.getCategoriesLists()
    },
    // id
    getParentPid(pid) {
      if (pid.length === 0) {
        this.pid = 0
      } else {
        this.pid = pid[pid.length - 1]
      }
      this.level = pid.length
    },
    // 
    addClassification() {
      this.$http.post('categories', {
        cat_pid: this.pid,
        cat_name: this.ruleForm.name,
        cat_level: this.level
      }).then(response => {
        if (response.data.meta.status === 201) {
          this.$message({
            message: '!',
            type: 'success'
          })
          this.getCategoriesLists()
          this.getCategories()
        } else {
          this.$message({
            message: '!',
            type: 'error'
          })
        }
      })
    },
    // 
    resetField() {
      this.$refs.ruleForm.resetFields()
      // 
      this.$refs.cascader.$refs.panel.clearCheckedNodes()
    },
    // 
    resetRulesField() {
      this.$refs.editRuleForm.resetFields()
    },
    // Id
    getCurrentId(id) {
      this.currentId = id
    },
    // 
    editClassification() {
      this.$http.put(`categories/${this.currentId}`, { cat_name: this.editRuleForm.name }).then(response => {
        if (response.data.meta.status === 200) {
          this.$message({
            message: '!',
            type: 'success'
          })
          this.getCategoriesLists()
          this.getCategories()
        } else {
          this.$message({
            message: '',
            type: 'error'
          })
        }
      })
    },
    // 
    deleteClass(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$http.delete(`categories/${id}`).then(response => {
          if (response.data.meta.status === 200) {
            this.$message({
              type: 'success',
              message: '!'
            })
            this.getCategoriesLists()
            this.getCategories()
          } else {
            this.$message({
              type: 'error',
              message: '!'
            })
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  width: 96%;
  background-color: #fff;
  margin: 20px auto 0 auto;
  padding: 20px;
  box-shadow: 0 0 6px #ccc;
  .addClass {
    margin-bottom: 20px;
  }
  .el-icon-success {
    color: #189fa9;
  }
}
</style>
