<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="container">
      <!--  -->
      <el-alert title=":!" type="warning" :closable="false" show-icon></el-alert>
      <!--  -->
      <div class="cascader">
        <span> :</span>
        <el-cascader v-model="value" :options="options" @change="getValue"></el-cascader>
      </div>
      <!-- tab -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="first">
          <!--  -->
          <el-button type="primary" size="small" @click="dialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="manyTableData" style="width: 100%" border @expand-change="expandSelect" :row-key='getRowKeys'
            :expand-row-keys="expands">>
            <el-table-column type="expand">
              <template slot-scope="props">
                <el-tag :key="tag" v-for="tag in props.row.valArry" closable :disable-transitions="false"
                  @close="handleClose(tag,props.row.valArry,props.row.attr_id,'many',props.row.attr_name)">
                  {{tag}}
                </el-tag>
                <el-input class="input-new-tag" v-if="inputVisible" v-model="inputValue" ref="saveTagInput" size="small"
                  @blur="handleInputConfirm(props.row.valArry,props.row.attr_id,'many',props.row.attr_name)">
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag
                </el-button>
              </template>
            </el-table-column>
            <el-table-column label="#" prop="idx" width="60">
            </el-table-column>
            <el-table-column label="" prop="attr_name">
            </el-table-column>
            <el-table-column label="" prop="">
              <template v-slot="scoped">
                <el-button type="primary" icon="el-icon-edit" class="changeBtn"
                  @click="editDialogVisible = true;editParams(scoped.row.idx,scoped.row.attr_id,'many')">
                </el-button>
                <el-button type="danger" icon="el-icon-delete" class="changeBtn"
                  @click="deleteParams(scoped.row.attr_id,'many')"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <!--  -->
          <el-button type="primary" size="small" @click="addDialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="onlyTableData" style="width: 100%" border @expand-change="expandSelect" :row-key='getRowKeys'
            :expand-row-keys="expands">
            <el-table-column type="expand">
              <template v-slot="props">
                <!-- <el-form label-position="left" inline class="demo-table-expand">
                  <span v-for="(item,index) in props.row.valArry" :key="index">
                    <el-tag :key="item" closable :type="''">{{item}}</el-tag>
                  </span>
                </el-form> -->
                <el-tag :key="tag" v-for="tag in props.row.valArry" closable :disable-transitions="false"
                  @close="handleClose(tag,props.row.valArry,props.row.attr_id,'only',props.row.attr_name)">
                  {{tag}}
                </el-tag>
                <el-input class="input-new-tag" v-if="inputVisible" v-model="inputValue" ref="saveTagInput" size="small"
                  @blur="handleInputConfirm(props.row.valArry,props.row.attr_id,'only',props.row.attr_name)">
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag
                </el-button>
              </template>
            </el-table-column>
            <el-table-column label="#" prop="idx" width="60">
            </el-table-column>
            <el-table-column label="" prop="attr_name">
            </el-table-column>
            <el-table-column label="" prop="">
              <template v-slot="scoped">
                <el-button type="primary" icon="el-icon-edit" class="changeBtn"
                  @click="editStaticDialogVisible = true;editParams(scoped.row.idx,scoped.row.attr_id,'only')">
                </el-button>
                <el-button type="danger" icon="el-icon-delete" class="changeBtn"
                  @click="deleteParams(scoped.row.attr_id,'only')"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible" width="45%" append-to-body @close="resetField">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="dialogVisible = false;addParams('many',ruleForm)"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="addDialogVisible" width="45%" append-to-body @close="resetAddField">
        <el-form :model="addStaticRuleForm" :rules="rules" ref="addStaticRuleForm" label-width="100px"
          class="demo-ruleForm">
          <el-form-item label="" prop="name">
            <el-input v-model="addStaticRuleForm.name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addDialogVisible = false;addParams('only',addStaticRuleForm)"> 
          </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="editDialogVisible" width="45%" append-to-body @close="resetEditField">
        <el-form :model="editRuleForm" :rules="rules" ref="editRuleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="name">
            <el-input v-model="editRuleForm.name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editDialogVisible = false;changeEditParams('many',editRuleForm)"> 
          </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="editStaticDialogVisible" width="45%" append-to-body
        @close="resetEditField">
        <el-form :model="editStaticRuleForm" :rules="rules" ref="editStaticRuleForm" label-width="100px"
          class="demo-ruleForm">
          <el-form-item label="" prop="name">
            <el-input v-model="editStaticRuleForm.name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editStaticDialogVisible = false"> </el-button>
          <el-button type="primary"
            @click="editStaticDialogVisible = false;changeEditParams('only',editStaticRuleForm)"> 
          </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: [],
      options: [],
      manyTableData: [],
      onlyTableData: [],
      activeName: 'first',
      // params
      many: 'many',
      only: 'only',
      // 
      inputVisible: false,
      inputValue: '',
      // id
      chooseId: 0,
      // 
      dialogVisible: false,
      // 
      addDialogVisible: false,
      // 
      editDialogVisible: false,
      // 
      editStaticDialogVisible: false,
      // idx,id
      currentEditId: 0,
      currentParamsId: 0,
      // 
      ruleForm: {
        name: ''
      },
      addStaticRuleForm: {
        name: ''
      },
      // 
      editRuleForm: {
        name: ''
      },
      // 
      editStaticRuleForm: {
        name: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      expands: [],
      getRowKeys(row) {
        return row.idx
      }
    }
  },
  created() {
    this.getCategories()
  },
  methods: {
    expandSelect: function (row, expandedRows) {
      if (expandedRows.length) {
        this.expands = []
        if (row) {
          this.expands.push(row.idx)
        }
      } else {
        this.expands = []
      }
    },
    // 
    deleteParams(attrId, sel) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$http.delete(`categories/${this.chooseId}/attributes/${attrId}`).then(response => {
          if (response.data.meta.status === 200) {
            this.getParamsList(sel, '')
          } else {
            this.$message({
              type: 'error',
              message: '!'
            })
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    changeEditParams(sel, form) {
      this.$http.put(`categories/${this.chooseId}/attributes/${this.currentParamsId}`,
        {
          attr_name: form.name,
          attr_sel: sel
        }).then(response => {
          if (response.data.meta.status === 200) {
            this.getParamsList(sel, '')
          } else {
            this.$message({
              message: '!',
              type: 'error'
            })
          }
        })
    },
    // id
    editParams(idx, id, sel) {
      this.currentEditId = idx
      this.currentParamsId = id
      if (sel === 'many') {
        this.editRuleForm.name = this.manyTableData[idx - 1].attr_name
      } else {
        this.editStaticRuleForm.name = this.onlyTableData[idx - 1].attr_name
      }
    },
    // 
    resetField() {
      this.$refs.ruleForm.resetFields()
    },
    resetAddField() {
      this.$refs.addStaticRuleForm.resetFields()
    },
    resetEditField() {
      // this.editParams(this.currentEditId)
    },
    // 
    handleClose(tag, valArry, attrId, sel, attrName) {
      valArry.splice(valArry.indexOf(tag), 1)
      this.$http.put(`categories/${this.chooseId}/attributes/${attrId}`,
        {
          attr_name: attrName,
          attr_sel: sel,
          attr_vals: valArry.join(',')
        }).then(response => {
          if (response.data.meta.status === 200) {
            this.getParamsList(sel, '')
          } else {
            this.$message({
              message: '!',
              type: 'error'
            })
          }
        })
    },
    // 
    showInput() {
      this.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 
    handleInputConfirm(valArry, attrId, sel, attrName) {
      const inputValue = this.inputValue
      if (inputValue) {
        valArry.push(inputValue)
      }
      this.inputVisible = false
      this.inputValue = ''
      this.$http.put(`categories/${this.chooseId}/attributes/${attrId}`,
        {
          attr_name: attrName,
          attr_sel: sel,
          attr_vals: valArry.join(',')
        }).then(response => {
          if (response.data.meta.status === 200) {
            this.getParamsList(sel, '')
          } else {
            this.$message({
              message: '!',
              type: 'error'
            })
          }
        })
    },
    // 
    handleClick(tab) {
      if (tab.label === '') {
        this.getParamsList(this.many)
      } else {
        this.getParamsList(this.only)
      }
    },
    // 
    getCategories() {
      this.$http.get('categories', { params: { type: 3 } }).then(response => {
        this.options = response.data.data
        function changelabel(options) {
          options.forEach(item => {
            item.value = item.cat_id
            item.label = item.cat_name
            if (item.children !== undefined) {
              changelabel(item.children)
            }
          })
        }
        changelabel(this.options)
      })
    },
    // 
    getValue(val) {
      this.chooseId = val[2]
      if (this.value.length !== 3) {
        this.value = ''
      }
      if (this.activeName === 'first') {
        this.getParamsList(this.many)
      } else {
        this.getParamsList(this.only)
      }
    },
    // 
    getParamsList(sel, message = '') {
      if (this.value === '') {
        this.$message({
          message: '!',
          type: 'error'
        })
      } else {
        this.$http.get(`categories/${this.value[2]}/attributes`, { params: { sel: sel } }).then(response => {
          if (response.data.meta.status === 200) {
            this.$message({
              message: message + '!',
              type: 'success'
            })
            if (sel === 'many') {
              this.manyTableData = response.data.data
              this.manyTableData.forEach((item, index) => {
                item.idx = index + 1
                item.valArry = item.attr_vals.length === 0 ? [] : item.attr_vals.split(',')
              })
            } else {
              this.onlyTableData = response.data.data
              this.onlyTableData.forEach((item, index) => {
                item.idx = index + 1
                item.valArry = item.attr_vals.length === 0 ? [] : item.attr_vals.split(',')
              })
            }
          } else {
            this.$message({
              message: '!',
              type: 'error'
            })
          }
        })
      }
    },
    // 
    addParams(sel, form) {
      this.$http.post(`categories/${this.chooseId}/attributes`, {
        attr_name: form.name,
        attr_sel: sel
      }).then(response => {
        if (response.data.meta.status === 201) {
          this.getParamsList(sel, '')
        } else {
          this.$message({
            message: '!',
            type: 'error'
          })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  width: 96%;
  background-color: #fff;
  margin: 20px auto 0 auto;
  padding: 20px;
  box-shadow: 0 0 6px #ccc;
  .cascader {
    margin-top: 20px;
    margin-bottom: 20px;
    span {
      margin-right: 15px;
    }
  }
  .el-button {
    margin-bottom: 20px;
  }
  .el-tag + .el-tag {
    margin: 0 10px 15px 10px;
  }
  .button-new-tag {
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
    margin: 0 10px 15px 10px;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
  }
  .changeBtn {
    margin-bottom: 0;
    width: 100px;
    height: 30px;
    line-height: 30px;
    padding: 0;
  }
}
</style>
