<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="container">
      <el-input placeholder="" v-model="searchContent" class="input-with-select">
        <el-button slot="append" icon="el-icon-search" @click="search()"></el-button>
      </el-input>
      <el-table :data="tableData" border style="width: 100%;margin-bottom: 20px">
        <el-table-column prop="idx" label="#" width="60">
        </el-table-column>
        <el-table-column prop="order_number" label="">
        </el-table-column>
        <el-table-column prop="order_price" label="" width="90">
        </el-table-column>
        <el-table-column prop="pay_status" label="" width="80">
          <template v-slot="scoped">
            <el-tag type="danger" v-if="scoped.row.pay_status === '0'"></el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="80">
        </el-table-column>
        <el-table-column label="" width="220">
          <template v-slot="scoped">
            {{scoped.row.create_time | dateFormat('yyyy-MM-dd hh:mm:ss')}}
          </template>
        </el-table-column>
        <el-table-column prop="" label="" width="200">
          <el-button type="primary" icon="el-icon-edit" class="operateBtn" @click="dialogVisible = true"></el-button>
          <el-button disabled type="success" icon="el-icon-location" class="operateBtn"
            @click="logisticsDialogVisible = true">
          </el-button>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible" width="45%" @close="resetField">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="/" prop="city">
            <el-cascader :options="cityOptions" v-model="ruleForm.city" class="cascader">
            </el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model="ruleForm.address"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="logisticsDialogVisible" width="45%">
        <el-timeline :reverse="false">
          <el-timeline-item v-for="(activity, index) in activities" :key="index" :timestamp="activity.timestamp">
            {{activity.content}}
          </el-timeline-item>
        </el-timeline>
        <span slot="footer" class="dialog-footer">
          <el-button @click="logisticsDialogVisible = false"> </el-button>
          <el-button type="primary" @click="logisticsDialogVisible = false"> </el-button>
        </span>
      </el-dialog>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pagenum"
        :page-sizes="[3, 5, 10, 15]" :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
// 
import cityOptions from '../../assets/city_data2017_element.js'
export default {
  data() {
    return {
      // 
      searchContent: '',
      // 
      tableData: [],
      // 
      pagenum: 1,
      // 
      pagesize: 5,
      // 
      total: 0,
      // 
      dialogVisible: false,
      logisticsDialogVisible: false,
      // 
      cityOptions: cityOptions,
      ruleForm: { city: '', address: '' },
      rules: {
        city: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      activities: [{
        content: '',
        timestamp: '2018-04-15'
      }, {
        content: '',
        timestamp: '2018-04-13'
      }, {
        content: '',
        timestamp: '2018-04-11'
      }],
      // id
      orderId: 0
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    // 
    getOrdersList() {
      this.$http.get('orders', {
        params: {
          query: this.searchContent,
          pagenum: this.pagenum,
          pagesize: this.pagesize
        }
      }).then(response => {
        if (response.data.meta.status === 200) {
          this.$message({
            message: '!',
            type: 'success'
          })
          this.total = response.data.data.total
          this.tableData = response.data.data.goods
          this.tableData.forEach((item, index) => {
            item.idx = index + 1
          })
        } else {
          this.$message({
            message: '!',
            type: 'error'
          })
        }
      })
    },
    // 
    handleSizeChange(val) {
      this.pagesize = val
      this.getOrdersList()
    },
    handleCurrentChange(val) {
      this.pagenum = val
      this.getOrdersList()
    },
    // 
    search() {
      this.getOrdersList()
    },
    // 
    resetField() {
      this.$refs.ruleForm.resetFields()
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  width: 96%;
  background-color: #fff;
  margin: 20px auto 0 auto;
  padding: 20px;
  box-shadow: 0 0 6px #ccc;
  .input-with-select {
    width: 500px;
    margin-bottom: 20px;
  }
  .operateBtn {
    height: 30px !important;
    line-height: 30px;
    padding: 0 20px;
  }
  .cascader {
    width: 100%;
  }
}
</style>
